import React, { useState } from 'react'
import { useDispatch, useSelector } from "react-redux";
import { addItem } from "../redux/slices/basketSlice";

function ProductCard({ id, price, title, text, size, url, url1, url2, url3}) {
  const [mainImage, setMainImage] = useState(url1);
  const [previousImage, setPreviousImage] = useState(url1);
  

  const handleImageClick = (url) => {
    setPreviousImage(mainImage);
    setMainImage(url);
  };  
    
    const dispatch = useDispatch();
    const basketItem = useSelector((state) => state.basketSlice.items.find((obj) => obj.id === id));
  
    const addedCount = basketItem ? basketItem.count : 0;
    
    const onClickAdd = () => {
      const item = {
        id,
        title,
        price,
        url,
      };
      dispatch(addItem(item));
      window.alert('Добавлено в корзину!');
    };

    return (
      <div className='ProductCard'>
        <div className='ProductCard__col'>
            <div className='ProductCard__col__mainImage'>
                <img src={mainImage} alt='' className='imageForClick'/>
            </div>
            <div className='ProductCard__col__rowImage'>
                <div className='ProductCard__col__rowImage__grey' onClick={() => handleImageClick(url2)}>
                    <img src={url2 ? url2 : previousImage} alt='' className='imageForClick'/>
                </div>
                <div className='ProductCard__col__rowImage__grey' onClick={() => handleImageClick(url3)}>
                    <img src={url3 ? url3 : previousImage} alt='' className='imageForClick'/>
                </div>
                <div className='ProductCard__col__rowImage__grey' onClick={() => handleImageClick(url)}>
                    <img src={url ? url : previousImage} alt='' className='imageForClick'/>
                </div>
            </div>
        </div>
        <div className='ProductCard__col'>
            <div className='ProductCard__col__rowTitle'>
                <h1>{title}</h1>
                <span className='size'>{size} мл</span>
            </div>
            <h2>серии "Ясноликая"</h2>
            <p>{text}</p>
            <div className='ProductCard__col__rowBtn'>
                <span className='price'>{price} ₽</span>
                <button onClick={onClickAdd}>Добавить в корзину</button>
            </div>
        </div>
      </div>
    )
  }
  
  export default ProductCard
